<template>
  <div>
    <!-- Recruit logo starts here -->
    <div class="align-items-center d-flex justify-content-between pb-4">
      <h6 class="header-pretitle mb-0">
        <b-img src="/img/recruitly/logo.svg" alt="logo" />
      </h6>
    </div>
    <!-- Recruit logo ends here -->
    <b-card class="mb-3">
      <b-row class="px-2">
        <!-- Page heading starts here -->
        <b-col md="12" class="px-2">
          <h2 class="mb-3">Jobs and Apply Job</h2>
        </b-col>
        <!-- Page heading ends here -->

        <!-- Global search starts here -->
        <b-col md="12" class="px-2">
          <!-- Search Filter Options starts here -->
          <b-input-group>
            <template #prepend>
              <b-input-group-text>
                <i class="fe fe-search" />
              </b-input-group-text>
            </template>
            <!-- Search by Job Title starts here -->
            <b-form-input class="w-25" placeholder="Search by job title" v-model="search_data"></b-form-input>
            <!-- Search by Job Title ends here -->

            <!-- Search by Location starts here -->
            <b-form-select v-model="selected_location" :options="locations_options" text-field="text" value-field="value" @change="getFilteredJobs()">
              <template #first>
                <b-form-select-option :value="null">Location</b-form-select-option>
              </template>
            </b-form-select>
            <!-- Search by Location ends here -->

            <!-- Search by Job Type starts here -->
            <b-form-select :options="job_type" v-model="selected_job_type" text-field="text" value-field="value" @change="getFilteredJobs()" v-if="job_type.length > 1">
              <template #first>
                <b-form-select-option :value="null">Job Types - All</b-form-select-option>
              </template>
            </b-form-select>
            <!-- Search by Job Type ends here -->

            <!-- Clear search results starts here -->
            <template #append>
              <b-button variant="white" class="text-secondary px-3" size="sm" @click="clearSearch()">
                <i class="fe fe-x " />
                Clear
              </b-button>
            </template>
            <!-- Clear search results ends here -->
          </b-input-group>
          <!-- Search Filter Options ends here -->
        </b-col>
        <!-- Global search ends here -->
      </b-row>
    </b-card>
    <!-- Show active jobs as cards starts here -->
    <b-card body-class="pb-3">
      <b-row>
        <b-col v-if="resultant_data.length <= 0">
          <b-card class="shadow-none card-inactive mb-3">
            <b-row>
              <b-col md="12" xl="7" class="mx-auto p-6 text-center">
                <b-img src="/img/no-jobs-found.svg" class="img-fluid mb-4 pb-2 pt-2" />
                <h2 class="mb-1 font-weight-bold">No jobs found</h2>
                <p class="text-muted mb-0 white-space">
                  Try adjusting your search or filter to find what you are looking for."
                </p>
              </b-col>
            </b-row>
          </b-card>
        </b-col>
        <b-col md="12" v-for="(data, index) in resultant_data" :key="index">
          <b-card class="mb-3 shadow-none">
            <!-- Job title starts here -->
            <b-link :href="`/recruithire/${data.id}`" target="_blank" class="text-hover-primary">
              <h3 class="font-weight-bold mb-2 text-truncate text-body">{{ data.job_title }}</h3>
            </b-link>
            <!-- Job title ends here -->

            <!-- Location, Job type, Job applicants, Job posted and Job openings block starts here-->
            <div class="d-flex align-items-center">
              <div class="d-flex flex-nowrap mr-4 text-gray-700" v-if="data.location" title="Locaton">
                <i class="fe fe-map-pin text-primary" />
                <p class="mb-0 ml-2 text-capitalize">{{ data.location }}</p>
              </div>
              <div class="d-flex flex-nowrap mr-4 text-gray-700" v-if="data.type" title="Job Type">
                <i class="fe fe-briefcase text-info" />
                <p class="mb-0 ml-2 text-capitalize">{{ data.type }}</p>
              </div>
              <div class="d-flex flex-nowrap mr-4 text-gray-700" title="Job Applicants">
                <i class="fe fe-users text-success" />
                <p class="mb-0 ml-2 text-capitalize">{{ data.no_of_applicants }}</p>
              </div>
              <div class="d-flex flex-nowrap mr-4 text-gray-700" v-if="data.created_date" title="Job Posted">
                <i class="fe fe-layout text-primary" />
                <p class="mb-0 ml-2 text-capitalize">{{ getLocalTime(data.created_date) }}</p>
              </div>
              <div class="d-flex flex-nowrap mr-4 text-gray-700" v-if="data.no_of_position" title="Job Openings">
                <i class="fe fe-book-open text-info" />
                <p class="mb-0 ml-2 text-capitalize">{{ data.no_of_position }}</p>
              </div>
            </div>
            <!-- Location, Job type, Job applicants, Job posted and Job openings block ends here -->

            <!-- Job description starts here -->
            <h4 class="mt-4 mb-2">Job description</h4>
            <p class="mb-3 align-middle text-gray-800 small">
              <!-- Read less description data starts here -->
              <span v-if="!validate_read_more[`description_${data.id}`]" v-html="readDescription(data.description, 'read_less')" />
              <!-- Read less description data ends here -->

              <!-- Read more description data starts here -->
              <span v-if="validate_read_more[`description_${data.id}`]" v-html="readDescription(data.description, 'read_more')" />
              <!-- Read more description data ends here -->

              <!-- Read more and less options starts here -->
              <b-link class="text-primary ml-1 cursor-pointer" v-if="data.description.length > 300">
                <span v-if="validate_read_more[`description_${data.id}`]" @click="$set(validate_read_more, `description_${data.id}`, false)" title="Read less description">Read less</span>
                <span v-else-if="!validate_read_more[`description_${data.id}`]" @click="$set(validate_read_more, `description_${data.id}`, true)" title="Read more description">Read more</span>
              </b-link>
              <!-- Read more and less options ends here -->
            </p>
            <!-- Job description ends here -->

            <div class="d-flex justify-content-between align-items-center mt-3 float-right">
              <!-- Apply Job option starts here -->
              <b-button variant="outline-secondary" size="sm" class="px-3 text-nowrap" @click="openModal(data.id)">
                <i class="align-bottom fe fe-check-circle mr-1" />
                Apply
              </b-button>
              <!-- Apply Job option ends here -->
            </div>
          </b-card>
        </b-col>
      </b-row>
    </b-card>
    <!-- Show active jobs as cards ends here -->

    <!-- Apply candidate sidebar starts here -->
    <b-sidebar id="add_candidate" title="Apply" right shadow body-class="bg-soft-light p-4" header-class="d-flex flex-row-reverse justify-content-between bg-white close-btn-right" width="360px">
      <!-- Add candidate form starts here -->
      <form ref="form" @submit.prevent="addCandidate">
        <b-row>
          <b-col md="12">
            <b-form-group label="First name">
              <b-form-input placeholder="Please enter first name" type="text" v-model="form.first_name" required />
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-form-group label="Email">
              <b-form-input placeholder="Please enter your email" type="email" v-model="form.email" required />
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-form-group label="Resume">
              <b-form-file
                @submit.stop.prevent
                @input="uploadResume"
                ref="file_input"
                accept=".doc,.docx,.odt,.txt,.pdf,.png,.rtf,.tex,.jpeg,.jpg,.xls,.xlsx,.mp4,.flv,.avi,.mov"
                class="mb-2"
                placeholder="Please upload your resume"
                drop-placeholder="Drop file here..."
                v-model="form.resume_upload"
              ></b-form-file>
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-form-group class="recaptcha-mr-0">
              <Recaptcha :key="render" />
              <span v-if="validate_recaptcha" class="text-danger">Please select recaptcha</span>
            </b-form-group>
          </b-col>
          <b-col md="12">
            <div class="d-flex justify-content-end border-top pt-4">
              <b-button variant="primary" type="submit" class="px-4 ml-3">{{ submit_button }}</b-button>
            </div>
            <div class="d-flex justify-content-end mt-2">
              <span v-if="error_message" class="badge badge-soft-danger">Duplicate entry</span>
            </div>
          </b-col>
        </b-row>
      </form>
      <!-- Add candidate form ends here -->
    </b-sidebar>
    <!-- Apply candidate sidebar ends here -->
  </div>
</template>
<script>
  import JobDetails from "@/js/recruit/job-details";
  import Candidate from "@/js/recruit/candidate";
  import FileManager from "@/js/core/file-manager";
  import Recaptcha from "../form/components/input/Recaptcha.vue";
  import DefaultData from "@/js/recruit/default-data";

  export default {
    components: { Recaptcha },
    data() {
      return {
        domain_id: "",
        group_id: "",
        job_type: [],
        selected_location: null,
        selected_job_type: null,
        locations_options: [],
        jobdetails: [],
        jobs_data: [],
        form: {
          first_name: "",
          email: "",
          resume_upload: "",
        },
        enable_delete_file: false,
        recaptcha_response_status: false,
        validate_recaptcha: false,
        submit_button: "Submit",
        error_message: false,
        render: 0,
        search_data: "",
        validate_read_more: {},
      };
    },
    computed: {
      // Get active jobs and search data
      resultant_data() {
        if (this.search_data) {
          return this.jobs_data.filter((data) => {
            return this.search_data
              .toLowerCase()
              .split(" ")
              .every((title) => data.job_title.toLowerCase().includes(title));
          });
        } else return this.jobs_data;
      },
    },
    mounted() {
      this.domain_id = window.domain;
      this.group_id = window.user;
      this.getActiveJobs();
    },
    methods: {
      // Clear the search results
      async clearSearch() {
        this.selected_location = null;
        this.selected_job_type = null;
        this.search_data = "";
        this.getFilteredJobs();
      },
      // Get Jobs by Location and Job-Type
      async getFilteredJobs() {
        let param = "";
        if (this.selected_location) param = `?location='${this.selected_location}'`;
        if (this.selected_job_type) param = `?type='${this.selected_job_type}'`;
        if (this.selected_location && this.selected_job_type) param = `?location='${this.selected_location}'&type='${this.selected_job_type}'`;
        try {
          this.jobs_data = await JobDetails.getFilteredJobs(this.domain_id, this.group_id, param);
        } catch (error) {
          this.jobs_data = [];
        }
      },
      // Get all active jobs
      async getActiveJobs() {
        // Get active job by group id and domain id
        this.jobs_data = await JobDetails.getActiveJobs(this.domain_id, this.group_id);

        // Get job types
        if (this.jobs_data.length > 0) {
          let job_types = [];
          // Get unique job type from active jobs data
          this.jobs_data.filter(function(item) {
            let data_index = job_types.findIndex((data) => data.text == item.type);
            if (data_index <= -1 && item.type != null) job_types.push({ text: item.type, value: item.type });
            return job_types;
          });
          // Assign unique job types
          this.job_type = job_types;
        }

        // Get only locations
        this.jobs_data.forEach((data) => {
          if (data.location) this.locations_options.push({ text: data.location, value: data.location });
        });

        // Get Unique locations
        if (this.locations_options.length > 0) {
          this.locations_options = this.locations_options.reduce((acc, current) => {
            const location = acc.find((item) => item.text.toLowerCase() === current.text.toLowerCase());
            if (!location) {
              current["text"] = current["text"].charAt(0).toUpperCase() + current["text"].slice(1);
              return acc.concat([current]);
            } else return acc;
          }, []);
        }
      },
      // Remove html tags from job description text
      getDescription(description) {
        return description.replace(/(<([^>]+)>)/g, "");
      },
      // Open add candidate modal
      openModal(job_id) {
        this.submit_button = "Submit";
        this.error_message = false;
        // Open candidate side bar
        this.$root.$emit("bv::toggle::collapse", "add_candidate");
        // Get recaptcha data
        this.$root.$on("recaptcha_status", this.getRecaptcha);
        this.form = {};
        this.form.job_id = job_id;
        this.enable_delete_file = false;
      },
      // Get uploaded resume URL
      async uploadResume(file) {
        // Get s3 url after uploading resume upload in add candidate form
        let upload_data = await FileManager.upload(`recruit/${this.group_id}`, "post-object", file, "", file.type, "public-read");
        let data = upload_data.presign_data;
        // Assign s3 url to form
        this.form.resume_upload = `${data.url}${data.fields.key}`;
      },
      // Add a candidate
      async addCandidate() {
        // check recaptcha response is true or not
        if (this.recaptcha_response_status) {
          this.submit_button = "Submitting";
          this.form.source = "website";
          try {
            let candidate_response = await Candidate.saveCandidate(this.domain_id, this.group_id, this.form);
            // Check candidate is added or not and if yes, close add candidate sidebar
            if (candidate_response.status) {
              this.submit_button = "Submitted";
              setTimeout(() => {
                ++this.render;
                this.recaptcha_response_status = false;
                this.$root.$emit("bv::toggle::collapse", "add_candidate");
              }, 500);
            }
          } catch (error) {
            ++this.render;
            this.recaptcha_response_status = false;
            this.submit_button = "Submit";
            this.error_message = true;
          }
        } else this.validate_recaptcha = true;
      },
      // Get recaptcha response
      getRecaptcha(response) {
        // Get response after selecting recaptcha in add candidate form
        this.recaptcha_response_status = response.success == true ? true : false;
        // Show validation message if recaptcha status is false
        this.validate_recaptcha = this.recaptcha_response_status ? false : true;
      },
      // Read more or less description
      readDescription(data, type) {
        return type == "read_more" ? data : data.substring(0, 300) + (data.length < 300 ? "" : "...");
      },
      // Get local time
      getLocalTime(value) {
        return DefaultData.dateFormat(value);
      },
    },
  };
</script>
<style>
  @import url("/css/sidebar/sidebar.css");
  .rounded-small {
    border-radius: 3px !important;
  }
  .close-btn-right strong {
    font-size: 1.1rem;
  }
  .recaptcha-mr-0 #g-recaptcha div {
    display: flex;
    width: inherit !important;
    height: inherit !important;
    margin: auto;
  }
  .multi-line-clamp-2 {
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .custom-control-inline {
    display: inline-flex;
    margin-right: 0rem !important;
    padding: 0.5rem 0.8rem !important;
  }
  .theme-modal .custom-radio .custom-control-label::before {
    border-radius: 50%;
    visibility: hidden !important;
  }
  .b-sidebar > .b-sidebar-body {
    padding-left: 1.5rem !important;
    padding-right: 1.5rem;
    padding-top: 1.5rem;
  }
  .text-hover-primary:hover .text-body {
    color: #2c7be5 !important;
  }
</style>
